import { createBottomTabNavigator } from '@react-navigation/bottom-tabs';
import * as React from 'react';

import TabBarIcon from '../components/TabBarIcon';
import TodoScreen from '../screens/TodoScreen';
import AddTaskScreen from '../screens/AddTaskScreen';
import DetailScreen from '../screens/DetailScreen';
import TabBarLabel from '../components/TabBarLabel';
const BottomTab = createBottomTabNavigator();
const INITIAL_ROUTE_NAME = 'Todo';

export default function BottomTabNavigator({ navigation, route }) {
  // Set the header title on the parent stack navigator depending on the
  // currently active tab. Learn more in the documentation:
  // https://reactnavigation.org/docs/en/screen-options-resolution.html
  navigation.setOptions({ headerTitle: getHeaderTitle(route) });

  return (
    <BottomTab.Navigator initialRouteName={INITIAL_ROUTE_NAME}>
      <BottomTab.Screen
        name="Todo"
        component={TodoScreen}
        options={{
          title: 'Todo List',
          tabBarIcon: ({ focused }) => <TabBarIcon focused={focused} name="ios-list" />,
          tabBarLabel: ({ focused }) => <TabBarLabel title={'Todo List'} focused={focused} />,
        }}
      />
      <BottomTab.Screen
        name="Add"
        component={AddTaskScreen}
        options={{
          title: 'Add Task',
          tabBarIcon: ({ focused }) => <TabBarIcon focused={focused} name="ios-add" />,
          tabBarLabel: ({ focused }) => <TabBarLabel title={'Add Task'} focused={focused} />,
        }}
      />
    </BottomTab.Navigator>
  );
}

function getHeaderTitle(route) {
  const routeName = route.state?.routes[route.state.index]?.name ?? INITIAL_ROUTE_NAME;

  switch (routeName) {
    case 'Todo':
      return 'Todo List:';
    case 'Add':
      return 'Create a new Task:';
    case 'Detail':
      return 'Task details:';
  }
}
