import * as WebBrowser from 'expo-web-browser'
import * as React from 'react'
import { Button, Platform, StyleSheet, Text, TouchableOpacity, View } from 'react-native'
import { CheckBox } from 'react-native-elements'
import { ScrollView } from 'react-native-gesture-handler'
import DetailScreen from './DetailScreen'
import { createStackNavigator } from '@react-navigation/stack'
import { Card } from 'react-native-material-ui';

const Stack = createStackNavigator()
export default function TodoScreen () {
  return (
    <Stack.Navigator screenOptions={{
      headerShown: false
    }}>
      <Stack.Screen name="TodoList" component={TodoListScreen} />
      <Stack.Screen name="Detail" component={DetailScreen} />
    </Stack.Navigator>
  )
}
class TodoListScreen extends React.Component {
  constructor (props) {
    super(props)
    this.focusListener = undefined
    this.state = { todoList: [] }
    this.focusListener = props.navigation.addListener('focus',
      () => this.componentGainsFocus())
  }

  updateTaskList () {
    fetch('https://plato.mrl.ai:8081/todo', {
      headers: {
        API: 'murray'
      }
    })
      .then(res => res.json())
      .then(body => {
        console.log(body)
        this.setState({ todoList: body.todo })
      })
  }

  componentGainsFocus () {
    if (this.props.route.params !== undefined) {
      const { task } = this.props.route.params

      console.log('Has focus')
      console.log(task)
      let currentList = this.state.todoList
      // Option 1: Push
      // currentList.push(task)
      currentList = [...this.state.todoList, task]
      this.setState({ todoList: currentList })
      // this.updateTaskList()
    }
  }

  componentWillUnmount () {
    this.props.navigation.removeListener('focus', this.componentGainsFocus)
  }

  componentDidMount () {
    this.updateTaskList()
  }

  removeTask (position) {
    fetch('https://plato.mrl.ai:8081/todo/remove', {
      method: 'POST',
      headers: {
        API: 'murray',
        'Content-Type': 'application/json',
        Accept: 'application/json'
      },
      body: JSON.stringify({ position: position })
    })
      .then(res => res.json())
      .then(body => {
        console.log(body)
        if (body.removed != undefined) {
          const currentList = this.state.todoList.filter((v, i) =>
            (i !== position))
          this.setState({ todoList: currentList })
        }
        // this.setState({todoList:body.todo})
      })
  }

  // Implement  completing the task on the server
  completeTask (position, state) {
    fetch('https://plato.mrl.ai:8081/todo/setState', {
      method: 'POST',
      headers: {
        API: 'murray',
        'Content-Type': 'application/json',
        Accept: 'application/json'
      },
      body: JSON.stringify(
        {
          position: position,
          status: state
        })
    })
      .then(res => res.json())
      .then(body => {
        console.log(body)
        if (body.updated != undefined) {
          const currentList = [...this.state.todoList]
          currentList[position].completed = state
          this.setState({ todoList: currentList })
        }
        // this.setState({todoList:body.todo})
      })
  }

  render () {
    return (
      <View style={styles.container}>
        <ScrollView style={styles.container} contentContainerStyle={styles.contentContainer}>
          <Card>

          {this.state.todoList.map((item, index) =>
            <View key={index} style={styles.todoView}>
              <CheckBox
                checked={item.completed}
                onPress={() => this.completeTask(index, !item.completed)}
                />
              <TouchableOpacity onPress={() => this.props.navigation.push('Detail', { task: item })} >
                <Text>{item.text} {item.completed ? 'COMPLETED' : ''}
                </Text>
              </TouchableOpacity>
              <View style={{ position: 'absolute', right: 0 }}>
                <Button title="X"
                  onPress={() => this.removeTask(index)}></Button>
              </View>
            </View>
          )}
        </Card>
        </ScrollView>
        <Button
          title="Add Task"
          onPress={() => this.props.navigation.navigate('Add')}/>
      </View>
    )
  }
}

TodoScreen.navigationOptions = {
  header: null
}

function DevelopmentModeNotice () {
  if (__DEV__) {
    const learnMoreButton = (
      <Text onPress={handleLearnMorePress} style={styles.helpLinkText}>
        Learn more
      </Text>
    )

    return (
      <Text style={styles.developmentModeText}>
        Development mode is enabled: your app will be slower but you can use useful development
        tools. {learnMoreButton}
      </Text>
    )
  } else {
    return (
      <Text style={styles.developmentModeText}>
        You are not in development mode: your app will run at full speed.
      </Text>
    )
  }
}

function handleLearnMorePress () {
  WebBrowser.openBrowserAsync('https://docs.expo.io/versions/latest/workflow/development-mode/')
}

function handleHelpPress () {
  WebBrowser.openBrowserAsync(
    'https://docs.expo.io/versions/latest/get-started/create-a-new-app/#making-your-first-change'
  )
}

